<template>
  <div class="drop-down">
    <button @click="down"><strong>{{desc}}</strong></button><i class="el-icon-arrow-down"></i>
    <ul :class="[dropFlag ? 'active': '']" id="drop">
      <li class="back" @click="removeDrop"><i class="el-icon-back"></i></li>
      
      <li v-for="(item, index) in opts" :key="index" v-if="item.head.length > 0">
        <ul>
          <li class="list-item list-header">{{item.head}}</li>
          <li v-for="(show, index) in item.items" :key="index" class="list-item"><router-link :to="{ name: 'show', params: { showId: show.id }}" @click.native="shua( show.id )">{{show.name}}</router-link></li>
          <hr>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        dropFlag: false
      }
    },
    props: ['opts', 'desc'],
    methods: {
      down (e) {
        this.dropFlag = !this.dropFlag
      },
      removeDrop (e) {
        if (this.dropFlag) {
          this.dropFlag = false
        }
      },
      shua (id) {
        const a = document.createElement('a')
        a.href = '/#runway/' + id
        a.click()
        // this.$router.push(obj)
        setTimeout(() => {
        //   console.log(window.location.href)
        //   this.$nextTick(() => window.location.reload())
          window.location.reload()
        }, 10)
      }
    }
  }
</script>

<style scoped>
  .drop-down {
    background-color: white;
    height: 40px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60vw;
    margin: 20px auto;
  }

  a {
    font-family: Tw, CH;
  }

  button {
    padding: 0 10px;
    /* padding-top: 10px; */
    border: none;
    /* height: 100%; */
    /* width: 92%; */
    flex: 1;
    background-color: white;

  }

  ul {
    background-color: white;
  }

  i {
    float: right;
    margin: 15px 5px 15px 0;
  }

  #drop {
    display: none;
    position: absolute;
    border: 1px solid darkgray;
    width:100%;
    height: 300px;
    overflow-y: auto;

    position: absolute;
    top: 80px;
    height: calc(100vh - 80px);
    padding: 20px 0;
  }

  #drop.active {
    display: block;
    border-top: 1px solid;
  }

  li {
    text-align: left;
    padding: 5px;
  }

  .list-header {
    margin: 3px 0;
    font-weight: bold;
    color:#cc141e;
  }

  .list-item {
    width: 100%;
    letter-spacing: .1em;
    color: #cc141e;
    font-family: Tw, CH;
    font-size: 12pt;
  }

  a {
    color: black;
    padding-left: 2px;
    text-decoration: none !important;
  }

  hr {
    margin: -3px;
  }

  .el-icon-back {
    border: 1px solid #DCDCD8;
    margin: -10px 0 10px;
    padding: 10px 4px;
    width: 44px;
    text-align: center;
    margin-right: 4px;
    line-height: 22px;
    color: #cc141e;
  }
  .el-icon-back i {
    float: none;
    margin: 0;
  }

</style>



