<template>
  <div ref="header">
   <div class="header" :class="{ shrink }">

      <router-link :to="{ name: 'Home'}"><h1>REGENERATION</h1></router-link>
      
      <i class="button-close" :class="{ hover }" @click="handleClick">
          <i class="bar bar-1"></i>
          <i class="bar bar-2"></i>
          <i class="bar bar-3"></i>
        </i>
    </div>

    <transition name="fade">
     <div class="navigation" v-if="hover">

      <!-- <div>
        <div class="search">
          <Select
            v-model="searchValue"
            placeholder="搜索"
            filterable
            remote
            @on-change="turnTo"
            :remote-method="searchMethod"
            :loading="loading">
            <OptionGroup label="Designer" v-if="resShow">
              <Option v-for="item in searchDesigners" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </OptionGroup>
            <OptionGroup label="Show">
              <Option v-for="item in searchShows" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </OptionGroup>
            <OptionGroup label="News" v-if="resShow">
              <Option v-for="item in searchNews" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </OptionGroup>
          </Select>
        </div>
      </div> -->



      <router-link :to="{ name: 'Home'}">HOME</router-link>
      <router-link :to="{ name: 'runway'}">RUNWAY</router-link>
      <router-link :to="{ name: 'designerHome' }">DESIGNER</router-link>
      <router-link :to="{ name: 'news'}">NEWS</router-link>
      <router-link :to="{ name: 'contact'}" exact>ABOUT US</router-link>


      <div style="margin: 5px 0 10px 0;">
        <AlphaDropDown :opts="optsAlpha" desc="SEARCH DESIGNERS" class="drop"></AlphaDropDown>
        <DropDown :opts="opts" desc="SEARCH SEASONS" class="drop"></DropDown>
      </div>
     </div>
    </transition>
  </div>
</template>

<script>
import DropDown from "./drop-down/DropDown.vue";
import AlphaDropDown from "./drop-down/AlphaDropDown.vue";
import {
  getAllDesigners,
  getAllNews,
  getAllShows,
  getSeasons,
  getAllBySeason,
  getAllByFirstName
} from "../service/getData";

export default {
  data() {
    return {
      searchValue: "",
      startSearch: false,
      resultModal: false,
      loading: false,
      resShow: false,
      designers: [],
      searchDesigners: [],
      shows: [],
      searchShows: [],
      news: [],
      searchNews: [],
      msg: "This is news page",
      curPathName: "",
      activeClass: "active",
      shrink: false,
      hover: false,
      opts: [
        {
          head: "",
          items: [
            {
              id: -1,
              name: ""
            }
          ]
        }
      ],
      optsAlpha: []
    };
  },
  components: {
    DropDown,
    AlphaDropDown
  },
  methods: {
    handleClick() {
      document.body.style.overflow = this.hover ? 'auto' : 'hidden'
      this.hover = !this.hover;
    },
    start(e) {
      this.startSearch = !this.startSearch;
    },
    turnTo(value) {
      console.log(value);
      let type = "";
      let id = "";
      for (let i = 0; i < value.length; i++) {
        if (value[i] === "-") {
          type = value.substring(0, i);
          id = value.substring(i + 1, value.length);
          break;
        }
      }
      if (type === "designer") {
        this.$router.push({
          name: "designerPro",
          params: { designerId: Number.parseInt(id) }
        });
      } else if (type === "show") {
        this.$router.push({
          name: "show",
          params: { showId: Number.parseInt(id) }
        });
      } else if (type === "news") {
        this.$router.push({
          name: "newsDetail",
          params: { newsId: Number.parseInt(id) }
        });
      }
    },
    searchMethod(query) {
      console.log(query);
      if (query !== "") {
        this.resShow = false;
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.searchDesigners = this.designers.map(item => {
            return {
              value: "designer-" + item.id,
              label: item.name
            };
          });
          this.searchDesigners = this.searchDesigners.filter(item =>
            item.label.toUpperCase().includes(query.toUpperCase())
          );
          this.searchShows = this.shows.map(item => {
            return {
              value: "show-" + item.id,
              label: item.name
            };
          });
          this.searchShows = this.searchDesigners.filter(item =>
            item.label.toUpperCase().includes(query.toUpperCase())
          );
          this.searchNews = this.news.map(item => {
            return {
              value: "news-" + item.newsId,
              label: item.title
            };
          });
          this.searchNews = this.searchNews.filter(item =>
            item.label.toUpperCase().includes(query.toUpperCase())
          );
          this.resShow = true;
          console.log(this.searchShows);
          console.log(this.searchNews);
          console.log(this.searchDesigners);
        }, 300);
      } else {
        this.designer = [];
      }
    },
    async initData() {
      getAllDesigners(0, 10000)
        .then(res => {
          if (res.code === "0") {
            this.designers = res.msg.designers;
          }
        })
        .catch(err => {
          console.log(err);
        });
      getAllNews(0, 10000)
        .then(res => {
          this.news = res.msg.newses;
        })
        .catch(err => {
          console.log(err);
        });
      getAllShows(0, 10000)
        .then(res => {
          this.shows = res.msg.shows;
        })
        .catch(err => {
          console.log(err);
        });
      getSeasons()
        .then(res => {
          console.log("seasons", res);
          for (let season of res.msg) {
            getAllBySeason(season.year, season.season)
              .then(res => {
                console.log("shows by season", res);
                this.opts.push({
                  head:
                    season.season === "1"
                      ? Number.parseInt(season.year) + 1 + " SS"
                      : season.year + " AW",
                  items: res.msg.shows
                });
              })
              .catch(err => {
                console.error(err);
              });
          }
        })
        .catch(err => {
          console.error(err);
        });
      getAllByFirstName()
        .then(res => {
          Object.keys(res.msg).forEach(key => {
            if (key !== "a") {
              this.optsAlpha.push({
                head: key,
                items: res.msg[key]
              });
            }
          });
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  mounted() {
    console.log("header");
    this.$refs.header.addEventListener('click', e => {
      if (e.target.tagName === 'A') {
        this.hover = false
        document.body.style.overflow = 'auto'
      }
    })
    window.addEventListener('scroll', e => {
      const shrink = document.body.getBoundingClientRect().y < -80

      if (shrink !== this.shrink) {
        this.shrink = shrink
      }
    })
    
    this.initData();

  }
};
</script>

<style scoped lang="scss">
@font-face {
  font-family: "Helvetica Neue Thin";
  src: url(../assets/fonts/HelveticaNeueLt.ttf);
}

@font-face {
  font-family: "Bernard";
  src: url(../assets/fonts/Bernard.ttf);
}

@font-face {
  font-family: "Tw";
  src: url(../assets/fonts/Tw.ttf);
}

.button-close {
  width: 18px;
  height: 13px;
  position: relative;
  padding: 10px;
  box-sizing: content-box;
  margin: -1px -10px 0 0;

  .bar {
    width: 18px;
    height: 2px;
    background: #cc141e;
    position: absolute;
    left: 10px;
    transform-origin: left center;
    transition: 0.2s;
  }

  .bar-1 {
    top: 9px;
  }
  .bar-2 {
    top: 15px;
  }
  .bar-3 {
    top: 21px;
  }

  &.hover {
    .bar {
      background: white;
      height: 1px;
      top: 10px;
    }
    .bar-1 {
      transform: translate3d(0, 6px, 0) rotateZ(-40deg) scaleX(0.5);
    }
    .bar-3 {
      top: 16px;
    }
    .bar-2 {
      top: 22px;
      transform: translate3d(2.5px, -3px, 0) rotateZ(40deg) scaleX(0.3);
    }
  }
}

/* header */
.header {
  margin: 0;
  // height: 21.6vw;
  // max-height: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  box-sizing: border-box;
  z-index: 10000;
  top: 0;
  position: fixed;
  width: 100vw;
  transition: .3s;

  &.shrink {
    height: 60px;
  }

  a {
    text-decoration: none !important;
  }
  h1 {
    transform: scaleY(0.75);
    color: #cc141e;
    font-size: 28px;
    font-family: Tw, CH;
    letter-spacing: 0.2em;
    text-align: center;
    margin: 0;
    text-decoration: none !important;
  }

  background: url("../../static/img/homeheader.jpg") no-repeat;
  background-position: center;
  background-size: cover;
}

.navigation {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background: #2D0206;
  z-index: 9999;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  justify-content: flex-start;
  padding-top: 80px;
}

.shrink .navigation {
  padding-top: 60px;
}

.navigation > a {    
    text-decoration: none;
    font-size: 1.1em;
    font-weight: bold;
    color: white;
    letter-spacing: .15em;
    padding: 0 0 0 20vw;
    margin: 0 auto;
    width: 100%;
    height: 15vh;
    // text-shadow: 1px 0 0px currentColor,0 1px 0px currentColor;
    display: flex;
    border-bottom: 2px solid black;
    align-items: center;
}

.fade-enter-active,
.fade-leave-active {
  transition: 0.2s ease-in-out;
  transform-origin: right;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
  // transform: rotateY(12deg);
  transform: translate3d(20%, 0, 0);
}
.fade-leave-to {
  transform: translate3d(-20%, 0, 0);
}
</style>




