<template>
  <div class="drop-down">
    <button @click="down"><strong>{{desc}}</strong></button><i class="el-icon-arrow-down"></i>
    <ul :class="[dropFlag ? 'active': '']" id="drop">
      <li v-for="item in opts">
        <ul id="main-content" :id=item.head>
          <li class="list-item list-header">{{item.head}}</li>
          <li v-for="show in item.items" class="list-item">
            <router-link :to="{name: 'designerPro', params: {designerId: show.id}}" @click.native="shua(show.id)">
              {{show.name}}
            </router-link>
          </li>
        </ul>
      </li>
    </ul>
    <ul :class="['nav', dropFlag ? 'active': '']">
      <li class="back" @click="removeDrop"><i class="el-icon-back"></i></li>
      <li v-for="char in alphas" @click="jump(char)">{{char}}</li>
    </ul>
  </div>
</template>

<script>
  import $ from 'jquery'

  const alphabet = Array(26).fill(1).map((_, i) => String.fromCharCode(65 + i))
  export default {
    data () {
      return {
        dropFlag: false,
        alphas: [...alphabet],
        top: 0
      }
    },
    props: ['opts', 'desc'],
    methods: {
      down (e) {
        this.dropFlag = !this.dropFlag
        this.alphas = [...alphabet].filter(x => $('#' + x).offset())
      },
      removeDrop (e) {
        if (this.dropFlag) {
          this.dropFlag = false
        }
      },
      jump (char) {
        $('#drop').scrollTop($('#' + char).offset().top - $('#drop').offset().top + $('#drop').scrollTop())
      },
      shua (id) {
        const a = document.createElement('a')
        a.href = '/#designer/' + id
        a.click()
        // this.$router.push(obj)
        setTimeout(() => {
        //   console.log(window.location.href)
        //   this.$nextTick(() => window.location.reload())
          window.location.reload()
        }, 10)
      }
    }
  }
</script>

<style scoped>
  .drop-down {
    background-color: white;
    height: 40px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60vw;
    margin: 20px auto;
  }

  a {
    font-family: Tw, CH;
    font-size: 12pt;
  }

  button {
    padding: 0 10px;
    /* padding-top: 10px; */
    border: none;
    /* height: 100%; */
    /* width: 92%; */
    flex: 1;
    background-color: white;
  }
  ul {
    background-color: white;
  }

  i {
    float: right;
    margin: 15px 5px 15px 0;
  }

  #drop {
    display: none;
    position: absolute;
    border: 1px solid darkgray;
    width:100%;
    overflow-y: auto;

    position: absolute;
    top: 80px;
    height: calc(100vh - 80px);
    padding: 20px 0;
  }

  #drop.active {
    display: block;
    border-top: 1px solid;
  }

  li {
    text-align: left;
    padding: 5px;
  }

  .list-header {
    margin: 3px 0;
    font-weight: bold;
    color: #cc141e;
  }

  .list-item {
    width: 100%;
    letter-spacing: .1em;
    color: #333;
    font-family: Tw, CH;
    font-size: 12pt;
    color: #cc141e;
    margin-top: 0pt;
  }

  a {
    color: black;
    padding-left: 2px;
    display: flex;
    text-decoration: none !important;
  }

  /* 侧边导航栏 */
  .nav {
    display: none;
    position: absolute;
    border: 1px solid #DCDCD8;
    padding:10px;
    font-size: 0.5em;
    right: 10px;
    top: 100px;
    color: #cc141e;
  }

  .nav > li {
    padding: 2px;
    cursor: pointer;
    font-size: 12pt;
    text-align: center;
  }

  .nav.active {
    display: block;
  }

  .el-icon-back {
    border-bottom: 1px solid #DCDCD8;
    margin: -10px 0 10px;
    padding: 10px 2px;
  }
  .el-icon-back i {
    float: none;
    margin: 0;
  }
</style>



